.block-intro {
    padding: 20px 0px 0px 0px;
    min-height: 680px;
    margin: 0px 0px -140px 0px;
    background-color: #fff;
    @media (max-width: 1500px) {
        position: relative;
    }
    @media (max-width: 992px) {
        margin: 0px 0px 0px 0px;
        padding: 44px 0px 0px 0px;
        min-height: 100%;
    }
    // .block-intro__top

    &__top {
        .container {
            position: relative;
        }
    }

    // .block-intro__bottom

    &__bottom {
    }
}
.top-block-intro {
    // .top-block-intro__bg

    &__bg {
        position: absolute;
        top: -52px;
        right: -322px;
        width: 100%;
        height: 688px;
        object-fit: cover;
        @media (max-width: 1500px) {
            filter: blur(3px);
            width: auto;
        }
        @media (max-width: 992px) {
            right: 0;
            height: 100%;
            width: auto;
            top: auto;
            bottom: 0;
        }
        @media (max-width: 768px) {
            filter: blur(0);
            height: auto;
            width: 100%;
            min-height: 180px;
        }
    }

    // .top-block-intro__content

    &__content {
        position: relative;
        z-index: 5;
        margin: 52px 82px 93px 90px;
        display: flex;
        flex-direction: column;
        align-items: start;
        @media (max-width: 1500px) {
            margin: 0px 0px 93px 0px;
        }
        @media (max-width: 992px) {
            margin: 0px 0px 0px 0px;
            padding: 0px 0px 100px 0px;
        }
        @media (max-width: 768px) {
            min-height: 616px;
        }
        p {
            font-weight: 400;
            font-size: 22px;
            line-height: 127%;
            margin: 40px 0px 0px 0px;
            max-width: 453px;
            @media (max-width: 768px) {
                font-size: 18px;
                margin: 0 auto;
                text-align: center;
                padding: 20px 0px 0px 0px;
            }
        }
        form {
            display: none;
            @media (max-width: 768px) {
                display: block;

                padding: 10px;
            }
        }
        .form__content {
            border-radius: 8px;
            background-color: rgba(255, 255, 255, 0.15);
            padding: 22px 20px 35px 20px;
        }
    }

    // .top-block-intro__buttons

    &__buttons {
        display: flex;
        gap: 0px 80px;
    }

    // .top-block-intro__call

    &__call {
        flex: 0 1 auto;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        line-height: 100%;
        text-transform: uppercase;
        text-align: center;
        border: 2px solid var(--theme-color-accent);
        border-radius: 3px;
        min-height: 80px;
        background-color: #fff;
        transition: background-color 0.3s;
        padding: 5px 40px;
        &:hover {
            background-color: var(--theme-color-accent);
        }
        @media (max-width: 768px) {
            display: none;
        }
    }

    // .top-block-intro__button

    &__button {
        @media (max-width: 768px) {
            display: none;
        }
    }

    // .top-block-intro__subtitles

    &__subtitles {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px 60px;
        margin: 35px 0px 56px 0px;
        .top-block-intro__subtitle {
            margin: 0px 0px 0px 0px;
        }
        @media (max-width: 480px) {
            grid-template-columns: 1fr;
            gap: 15px;
            margin: 20px 0px 24px 0px;
        }
    }
    // .top-block-intro__subtitle

    &__subtitle {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 22px;
        line-height: 127%;
        margin: 35px 0px 56px 0px;
        span {
            max-width: 530px;
            @media (max-width: 768px) {
                max-width: 295px;
            }
        }
        @media (max-width: 768px) {
            font-size: 14px;
            line-height: 143%;
            margin: 20px 0px 24px 0px;
        }
    }

    // .top-block-intro__icon

    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 0 0 40px;
        height: 40px;
        border-radius: 3px;
        background-color: var(--theme-color-text-common);
        margin: 0px 25px 0px 0px;
        @media (max-width: 768px) {
            flex: 0 0 30px;
            height: 30px;
            margin: 0px 15px 0px 0px;
        }
        img {
            width: 20px;
            height: 18px;
            @media (max-width: 768px) {
                width: 14px;
                height: 14px;
            }
        }
    }
}
.bottom-block-intro {
    margin: 0px 0px -140px 0px;
    @media (max-width: 992px) {
        margin: 0px 0px 0px 0px;
    }
    .container {
        @media (max-width: 1500px) {
            padding: 0px 0px 0px 0px;
        }
    }

    // .bottom-block-intro__content

    &__content {
        position: relative;
        z-index: 1;
        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 55px;
            height: 100%;
            width: calc(100% - 110px);
            border-radius: 10px;
            background-color: #fff;
            box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
            @media (max-width: 1500px) {
                top: 10px;
                left: 20px;
                width: calc(100% - 40px);
            }
        }
    }

    // .bottom-block-intro__body

    &__body {
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: space-between;
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
        gap: 45px;
        padding: 15px 110px 15px 85px;
        @media (max-width: 1500px) {
            padding: 40px 20px 22px 20px;
            gap: 15px;
        }
        @media (max-width: 992px) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
        }
    }

    // .bottom-block-intro__column

    &__column {
        flex: 0 1 260px;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding: 35px 0px 42px 0px;
        @media (max-width: 992px) {
            flex: 0 1 50%;
            padding: 10px 0px;
        }
        p {
            position: relative;
            z-index: 5;
            font-weight: 400;
            font-size: 16px;
            line-height: 137%;
            text-align: center;
            opacity: 0.7;
            @media (max-width: 768px) {
                font-size: 13px;
                line-height: 138%;
            }
        }
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            background-color: var(--theme-color-grey);
            z-index: 1;
            clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
        }
    }

    // .bottom-block-intro__icon

    &__icon {
        position: relative;
        z-index: 5;
        width: 80px;
        height: 80px;
        margin: 0px 0px 20px 0px;
        @media (max-width: 768px) {
            margin: 0px 0px 13px 0px;
        }
    }

    // .bottom-block-intro__title

    &__title {
        position: relative;
        z-index: 5;
        margin: 0px 0px 11px 0px;
        font-size: 22px;
        line-height: 127%;
        text-align: center;
        @media (max-width: 768px) {
            font-size: 18px;
            line-height: 133%;
            margin: 0px 0px 6px 0px;
        }
    }
}

.block-services {
    background-color: var(--theme-color-grey);
    padding: 252px 0px 220px 0px;
    @media (max-width: 992px) {
        padding: 70px 0px 65px 0px;
    }
}

.block-masters {
    padding: 0px 0px 220px 0px;
    @media (max-width: 992px) {
        padding: 0px 0px 65px 0px;
    }
}

.block-slider1 {
    .arrow-slider {
        top: 140px;
        transform: translate(0px, 0px);
        @media (max-width: 1330px) {
            top: 0;
        }
    }
    // .block-slider1__content

    &__content {
        position: relative;
    }

    // .block-slider1__slider

    &__slider {
        @media (max-width: 992px) {
            margin: 0px -20px;
            padding: 0px 20px;
        }
    }

    // .block-slider1__wrapper

    &__wrapper {
    }

    // .block-slider1__slide

    &__slide {
        p {
            font-weight: 400;
            font-size: 16px;
            line-height: 137%;
            color: rgba(43, 42, 41, 0.7);
            margin: 13px 0px 0px 0px;
            @media (max-width: 768px) {
                font-size: 14px;
                line-height: 143%;
            }
        }
        &:hover {
            .block-slider1__images {
                &::before {
                    content: "";
                    box-shadow: inset 4px -4px 0 0 #2b2a29;
                }
            }
            .block-slider1__arrow {
                background-color: var(--theme-color-text-common);
                img {
                    filter: brightness(0) invert(1);
                }
            }
            .block-slider1__more {
                background-color: var(--theme-color-text-common);
                color: #fff;
            }
        }
    }

    // .block-slider1__title

    &__title {
        font-weight: 500;
        font-size: 22px;
        line-height: 127%;
        text-align: center;
        margin: 34px 8px 0px 8px;
        @media (max-width: 768px) {
            font-size: 18px;
            line-height: 133%;
            margin: 23px 0px 0px 0px;
        }
    }

    // .block-slider1__subtitle

    &__subtitle {
        font-weight: 500;
        line-height: 136%;
        font-size: 18px;
        color: rgba(43, 42, 41, 0.7);
        text-align: center;
        margin: 5px 0px 0px 0px;
    }

    // .block-slider1__images

    &__images {
        position: relative;
        height: 410px;
        border-radius: 0px 10px 0px 10px;
        overflow: hidden;
        @media (max-width: 480px) {
            height: 340px;
        }
        &::before {
            content: "";
            position: absolute;
            z-index: 2;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            box-shadow: inset 4px -4px 0 0 #fecc00;
            transition: box-shadow 0.3s;
        }
    }

    // .block-slider1__pic

    &__pic {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // .block-slider1__arrow

    &__arrow {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 60px;
        height: 60px;
        background-color: var(--theme-color-accent);
        border-radius: 0px 10px 0px 10px;
        transition: background-color 0.3s;
        img {
            transition: filter 0.3s;
            width: 11px;
            height: 16px;
        }
    }

    // .block-slider1__more

    &__more {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 12px;
        line-height: 133%;
        text-transform: uppercase;
        text-align: center;
        padding: 15px 25px 13px 25px;
        background-color: var(--theme-color-accent);
        border-radius: 0px 10px 0px 10px;
        transition:
            background-color 0.3s,
            color 0.3s;
    }
}

.block-calculate {
    margin: -120px 0px 0px 0px;
    padding: 0px 0px 102px 0px;
    @media (max-width: 992px) {
        margin: 0px 0px 0px 0px;
        padding: 0px 0px 72px 0px;
    }
    &.call {
        .block-calculate__body {
            justify-content: center;
            @media (max-width: 1500px) {
                flex-wrap: wrap;
            }
            .btn {
                @media (max-width: 768px) {
                    width: calc(100% - 20px);
                    margin: 25px 0px 0px -20px;
                    min-width: auto;
                }
            }
        }
        .block-calculate__content {
            min-height: 210px;
        }
        .block-calculate__text {
            flex: 0 1 370px;
            font-size: 22px;
            @media (max-width: 992px) {
                flex: 1 1 auto;
            }
            @media (max-width: 768px) {
                font-size: 14px;
                line-height: 143%;
            }
        }
    }
    // .block-calculate__content

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 250px;
        position: relative;
        border-radius: 10px;
        background-color: var(--theme-color-accent);
        box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
        padding: 15px 135px 15px 88px;
        @media (max-width: 1500px) {
            padding: 10px 50px 10px 50px;
        }
        @media (max-width: 992px) {
            padding: 10px;
        }
        &::before {
            content: "";
            position: absolute;
            top: 0px;
            left: 55px;
            height: calc(100% + 15px);
            width: calc(100% - 110px);
            border-radius: 10px;
            background-color: #fff;
            box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
            z-index: -1;
            @media (max-width: 1500px) {
                left: 20px;
                height: calc(100% + 10px);
                width: calc(100% - 40px);
            }
        }
        &::after {
            content: "";
            position: absolute;
            top: 15px;
            left: 50%;
            transform: translate(-50%, 0px);
            width: 640px;
            height: calc(100% - 30px);
            background-color: #fff;
            opacity: 0.15;
            box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
            clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);
            z-index: 1;
            @media (max-width: 992px) {
                display: none;
            }
        }
    }

    // .block-calculate__body

    &__body {
        position: relative;
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px 50px;
        @media (max-width: 992px) {
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: center;
            gap: 0px;
            border-radius: 8px;
            background-color: rgba(255, 255, 255, 0.15);
            padding: 20px 20px 40px 20px;
        }
        .title2 {
            flex: 0 1 410px;
            @media (max-width: 992px) {
                flex: 1 1 auto;
                margin: 0px 0px 20px 0px;
                text-align: center;
            }
            @media (max-width: 768px) {
                font-size: 20px;
                line-height: 120%;
            }
        }
        .btn {
            padding: 10px 40px 10px 40px;
            margin: 0px 0px 0px 20px;
            @media (max-width: 1500px) {
                margin: 0px 0px 0px 0px;
            }
            @media (max-width: 992px) {
                margin: 25px 0px 0px 0px;
            }
            @media (max-width: 768px) {
                min-width: 100%;
                margin: 25px 0px 0px 0px;
            }
        }
    }

    // .block-calculate__text

    &__text {
        flex: 0 1 500px;
        display: flex;
        font-weight: 500;
        font-size: 28px;
        line-height: 127%;
        @media (max-width: 992px) {
            position: relative;
            flex: 1 1 auto;
            span {
                position: relative;
                z-index: 3;
                @media (max-width: 992px) {
                    max-width: 390px;
                }
                @media (max-width: 768px) {
                    max-width: 250px;
                }
            }
            &::before {
                content: "";
                position: absolute;
                top: -10px;
                left: 50%;
                transform: translate(-50%, 0px);
                width: 100%;
                height: calc(100% + 24px);
                background-color: #fff;
                opacity: 0.15;
                box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
                clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);
                z-index: 1;
            }
        }
        @media (max-width: 768px) {
            font-size: 14px;
            line-height: 143%;
        }
        img {
            position: relative;
            z-index: 3;
            flex: 0 0 40px;
            width: 40px;
            height: 40px;
            margin: 8px 25px 0px 0px;
            @media (max-width: 768px) {
                margin: 0px 15px 0px 0px;
            }
        }
    }

    // .block-calculate__buttons

    &__buttons {
        display: flex;
        gap: 30px 80px;
        @media (max-width: 992px) {
            flex-direction: column;
            width: 100%;
            .btn {
                width: 100%;
            }
        }
        .btn {
            margin: 0px 0px 0px 0px;
            @media (max-width: 992px) {
                width: calc(100% - 50px);
                min-width: calc(100% - 50px);
            }
        }
        .btn-yellow {
            box-shadow: none;
            border: 2px solid var(--theme-color-text-common);
            min-width: 292px;
            background-color: transparent;
            @media (max-width: 992px) {
                width: 100%;
                min-width: 100%;
            }
        }
    }
}

