        }
        @media (min-width: 992px) {
            display: flex;
            flex-direction: column;
        }
        @media (max-width: 992px) {
            text-align: center;
        }
    }
}

.block-about {
    padding: 0px 0px 98px 0px;
    @media (max-width: 992px) {
