            right: 0;
            height: auto;
            width: 100%;
        }
        @media (max-width: 768px) {
            filter: blur(0px);
        }
    }
    .top-block-intro__subtitle span {
        max-width: 580px;
    }
    .top-block-intro__subtitle {
        margin: 35px 0px 85px 0px;
        @media (max-width: 992px) {
            margin: 20px 0px 24px 0px;
        }
    }
    .top-block-intro__buttons {
        gap: 40px;
    }
    .top-block-intro__button {
        min-height: 76px;
        min-width: 280px;
        padding: 5px 10px;
    }
    .block-reviews {
        background-color: var(--theme-color-grey);
        margin: 0px 0px 0px 0px;
        padding: 102px 0px 110px 0px;
        box-shadow: inset 0 -20px 20px 0 rgba(43, 42, 41, 0.1);
        @media (max-width: 992px) {
            padding: 70px 0px;
        }
    }
    .block-slider2__content {
        margin: 0px 0px 0px 0px;
    }
    .block-examples-work {
        background-color: var(--theme-color-grey);
        padding: 102px 0px 102px 0px;
        @media (max-width: 992px) {
            padding: 70px 0px;
        }
    }
    .block-reviews-image {
        background-color: #fff;
        box-shadow: none;
    }
    .block-examples-work__title {
        margin: 0px 0px 18px 0px;
        @media (max-width: 768px) {
            margin: 0px 0px 10px 0px;
        }
    }
    .block-intro__top .container {
        position: static;
    }
    .block-request {
        margin: -280px 0px 0px 0px;
        position: relative;
        box-shadow: inset 0 -20px 20px 0 rgba(43, 42, 41, 0.1);
        @media (max-width: 992px) {
            margin: 0px 0px 0px 0px;
        }
        &::before {
            content: "";
            background-color: var(--theme-color-grey);
            top: 280px;
            width: 100%;
            height: calc(100% - 280px);
            position: absolute;
            z-index: -1;
            @media (max-width: 992px) {
                top: 0;
                height: 100%;
            }
        }
    }
    .block-request__pic {
        margin: -45px 0px 0px 0px;
        @media (max-width: 1500px) {
            margin: 0px 0px -30px 0px;
        }
        @media (max-width: 992px) {
            margin: 0px 0px -10px 0px;
        }
    }
    .block-slider2__bottom {
        margin: 110px 0px 0px 0px;
        @media (max-width: 992px) {
            margin: 50px 0px 0px 0px;
        }
    }
    .block-request__content {
        flex-direction: row-reverse;
        padding: 0px 15px 0px 85px;
        @media (max-width: 1500px) {
            padding: 0px 0px 30px 0px;
        }
        @media (max-width: 992px) {
            flex-direction: column-reverse;
        }
        @media (max-width: 480px) {
            padding: 0px 0px 0px 0px;
        }
        &::after {
            content: "";
            left: auto;
            right: 15px;
            clip-path: polygon(0 0, 100% 0, 100% 100%, 18% 100%);
        }
    }
    .block-calculate {
        margin: 0px 0px 0px 0px;
        .container {
            max-width: 1500px;
        }
    }
    .block-calculate__content {
        border-radius: 0px 0px 10px 10px;
        min-height: 210px;
        @media (max-width: 1330px) {
            padding: 30px 50px 30px 50px;
        }
        @media (max-width: 992px) {
            padding: 15px;
        }
        .title2 {
            @media (max-width: 1330px) {
                flex: 1 1 auto;
                text-align: center;
            }
        }
        &::after {
            content: "";
            left: auto;
            right: 35px;
            transform: translate(0px, 0px);
            width: 70%;
            clip-path: polygon(8% 0%, 100% 0%, 95% 100%, 0% 100%);
            @media (max-width: 1330px) {
                right: auto;
                left: 50%;
                transform: translate(-50%, 0px);
            }
        }
    }
    .block-calculate__body {
        @media (max-width: 1330px) {
            flex-wrap: wrap;
            justify-content: center;
        }
    }
}

.block-cases {
    padding: 102px 0px 395px 0px;
    @media (max-width: 992px) {
        padding: 70px 0px;
    }
    // .block-cases__content

    &__content {
        position: relative;
    }

    // .block-cases__slider

    &__slider {
        @media (max-width: 992px) {
            margin: 0px -20px;
            padding: 0px 20px;
        }
    }

    // .block-cases__wrapper

    &__wrapper {
    }

    // .block-cases__slide

    &__slide {
        display: flex;
        height: auto;
    }

    // .block-cases__item

    &__item {
        display: flex;
        flex-direction: column;
        gap: 30px;
        @media (max-width: 768px) {
            gap: 20px;
        }
    }

    // .block-cases__top

    &__top {
        border-radius: 10px;
        background-color: var(--theme-color-grey);
        padding: 40px 115px 45px 50px;
        display: flex;
        align-items: center;
        @media (max-width: 1330px) {
            padding: 30px;
        }
        @media (max-width: 768px) {
            padding: 20px;
            align-items: start;
        }
    }

    // .block-cases__photo

    &__photo {
        margin: 0px 30px 0px 0px;
        position: relative;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            bottom: -2px;
            border-radius: 50%;
            box-shadow:
                inset 0 0 20px 0 rgba(43, 42, 41, 0.15),
                inset 0 -4px 0 0 #fecc00;
        }
        img {
            width: 80px;
            height: 80px;
            object-fit: cover;
            border-radius: 50%;
        }
    }

    // .block-cases__right

    &__right {
        p {
            font-weight: 400;
            font-size: 18px;
            line-height: 139%;
            @media (max-width: 768px) {
                font-size: 14px;
            }
        }
    }

    // .block-cases__data

    &__data {
        font-size: 13px;
        line-height: 123%;
        text-transform: uppercase;
        margin: 0px 0px 8px 0px;
    }

    // .block-cases__bottom

    &__bottom {
        display: grid;
        gap: 30px;
        grid-template-columns: repeat(3, 1fr);
        @media (max-width: 768px) {
            gap: 10px;
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 480px) {
            grid-template-columns: 1fr;
        }
    }

    // .block-cases__image

    &__image {
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        span {
            position: absolute;
            left: 20px;
            bottom: 20px;
            border-radius: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: var(--theme-color-text-common);
            font-weight: 700;
            font-size: 22px;
            line-height: 127%;
            color: #fff;
            padding: 7px 30px 7px 30px;
            min-width: 130px;
            &.yellow {
                background-color: var(--theme-color-accent);
                color: var(--theme-color-text-common);
            }
            @media (max-width: 1330px) {
                bottom: 10px;
                left: 10px;
                font-size: 18px;
            }
            @media (max-width: 768px) {
                min-width: auto;
                padding: 5px 10px;
                font-size: 14px;
            }
        }
        img {
            width: 100%;
            height: 100%;
            border-radius: 10px;
            object-fit: cover;
        }
        &.image2 {
            display: grid;
            gap: 20px;
            grid-template-columns: repeat(2, 1fr);
            @media (max-width: 768px) {
                gap: 5px;
            }
        }
        &.image1 {
            @media (max-width: 480px) {
                height: 180px;
            }
        }
        &.image3 {
            @media (max-width: 768px) {
                grid-column: 1 / -1;
                height: 300px;
            }
            @media (max-width: 480px) {
                height: 180px;
            }
        }
    }
}
